module.exports = [[module.id, "/* You can add global styles to this file, and also import other style files */\n* {\n  box-sizing: border-box;\n}\nbody {\n  font-family: -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto, Helvetica,\n    Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n  font-size: 14px;\n  color: #333;\n  box-sizing: border-box;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  margin: 8px 0;\n}\np {\n  margin: 0;\n}\n.spacer {\n  flex: 1;\n}\n/* Style the body */\n/* Header/logo Title */\n.header {\n  padding: 10px;\n  text-align: center;\n  background-color: #1976d2;\n  color: white;\n  height: 60px;\n  margin: 0px;\n  display: flex;\n  align-items: center;\n  font-weight: 600;\n}\n/* Increase the font size of the heading */\n.header h1 {\n  font-size: 24px;\n}\n.filter {\n  padding: 5px 5px;\n  float: right;\n  flex: auto;\n  flex-direction: row;\n  font-weight: bold;\n  font-size: 16px;\n}\n.filter input {\n  width: 350px !important;\n}\n/* Sticky navbar - toggles between relative and fixed, depending on the scroll position. It is positioned relative until a given offset position is met in the viewport - then it \"sticks\" in place (like position:fixed). The sticky value is not supported in IE or Edge 15 and earlier versions. However, for these versions the navbar will inherit default position */\n.navbar {\n  overflow: hidden;\n  background-color: #333;\n  position: sticky;\n  position: -webkit-sticky;\n  top: 0;\n}\n/* Style the navigation bar links */\n.navbar a {\n  float: left;\n  display: block;\n  color: white;\n  text-align: center;\n  padding: 14px 20px;\n  text-decoration: none;\n}\n/* Right-aligned link */\n.navbar a.right {\n  float: right;\n}\n/* Change color on hover */\n.navbar a:hover {\n  background-color: #ddd;\n  color: black;\n}\n/* Active/current link */\n.navbar a.active {\n  background-color: #666;\n  color: white;\n}\n/* Column container */\n.row { /* IE10 */\n  display: flex; /* IE10 */\n  flex-wrap: wrap;\n}\n/* Create two unequal columns that sits next to each other */\n/* Sidebar/left column */\n.side { /* IE10 */\n  flex: 20%;\n  background-color: #f1f1f1;\n  padding: 20px;\n}\n/* Main column */\n.main { /* IE10 */\n  flex: 80%;\n  background-color: white;\n  padding: 20px;\n}\n/* Fake image, just for this example */\n.fakeimg {\n  background-color: #aaa;\n  width: 100%;\n  padding: 20px;\n}\n/* Footer */\n.footer {\n  padding: 20px;\n  text-align: center;\n  background: #ddd;\n}\n.header img {\n  margin: 0 16px;\n}\n.tab {\n  float: left;\n  border: 1px solid #ccc;\n  width: 100%;\n  height: 100%;\n  overflow-x: hidden;\n}\n/* Style the buttons inside the tab */\n.tab a {\n  display: block;\n  background-color: inherit;\n  color: #0e0d0d;\n  padding: 15px 10px;\n  width: 100%;\n  border: none;\n  outline: none;\n  text-align: left;\n  cursor: pointer;\n  transition: 0.3s;\n  font-size: 14px;\n}\n/* Change background color of buttons on hover */\n.tab a:hover {\n  color: #888;\n}\n/* Create an active/current \"tab button\" class */\n.tab a.active {\n  background-color: #ccc;\n}\n/* Style the tab content */\n.tabcontent {\n  float: left;\n  padding: 0px 5px;\n  border: 1px solid #ccc;\n  width: 85%;\n  border-left: none;\n  height: 100%;\n}\n.btn {\n  background-color: DodgerBlue; /* Blue background */\n  border: none; /* Remove borders */\n  color: white; /* White text */\n  padding: 12px 16px; /* Some padding */\n  font-size: 16px; /* Set a font size */\n  cursor: pointer; /* Mouse pointer on hover */\n}\n/* Darker background on mouse-over */\n.btn:hover {\n  background-color: RoyalBlue;\n}\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n  width: 100%;\n  border: 1px solid #ddd;\n}\ntd {\n  text-align: left;\n  padding: 16px;\n}\nth {\n  text-align: center;\n  padding: 16px;\n  color: rgb(212, 212, 212);\n  background-color: rgb(12, 12, 12);\n  cursor: pointer;\n  font-weight: bold;\n}\ntr:nth-child(even) {\n  background-color: #f2f2f2;\n}\n.tr-total {\n  background-color: lightblue !important;\n}\n.td-right {\n  text-align: right;\n  padding-right: 15px;\n}\n.td-center {\n  text-align: center;\n  padding-right: 5px;\n  padding-left: 5px;\n}\n.save {\n  display: flex;\n  width: 50%;\n  float: left;\n}\n.component-heading {\n  display: flex;\n  margin: 5px 5px;\n}\n.component-heading h1 {\n  width: 80%;\n  flex: auto;\n  font-size: 20px;\n}\n.component-heading button {\n  text-align: right;\n}\n.form-container {\n    padding: 16px;    \n  }\n/* Full-width input fields */\n.form input[type=text], input[type=password] {\n    width: 100%;\n    padding: 15px;\n    margin: 5px 0 22px 0;\n    display: inline-block;\n    border: none;\n    background: #f1f1f1;\n  }\n.form input[type=text]:focus, input[type=password]:focus {\n    background-color: #ddd;\n    outline: none;\n  }\n/* Overwrite default styles of hr */\nhr {\n    border: 1px solid #f1f1f1;\n    margin-bottom: 25px;\n  }\n/* Set a style for the submit/register button */\n.btn-submit {\n    background-color: #4CAF50;\n    color: white;\n    padding: 16px 20px;\n    margin: 10px 0;\n    border: none;\n    cursor: pointer;\n    width: 15%;\n    opacity: 0.9;\n  }\n.btn-submit:hover {\n    opacity:1;\n  }\n.btn-cancel {\n    background-color: rgba(250, 152, 86, 0.904);\n    color: white;\n    padding: 16px 20px;\n    margin: 10px 10px;\n    border: none;\n    cursor: pointer;\n    width: 20%;\n    opacity: 0.9;\n  }\n@media screen and (max-height: 450px) {\n  .sidenav {\n    padding-top: 15px;\n  }\n  .sidenav a {\n    font-size: 18px;\n  }\n}\n/* Responsive layout - when the screen is less than 700px wide, make the two columns stack on top of each other instead of next to each other */\n@media screen and (max-width: 700px) {\n  .row {\n    flex-direction: column;\n  }\n}\n/* Responsive layout - when the screen is less than 400px wide, make the navigation links stack on top of each other instead of next to each other */\n@media screen and (max-width: 400px) {\n  .navbar a {\n    float: none;\n    width: 100%;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]